import React,{Component} from 'react';
import {withRouter} from 'react-router-dom';
import "./Search.css";

const url = "https://foodflyapi.herokuapp.com/city";
const rurl= "https://foodflyapi.herokuapp.com/rest?city="

class Search extends Component{
    constructor(props){
        super(props)

        this.state={
            city:'',
            rest:''
           
            
        }
    }
    
    renderCity = (data) =>{
        
        if(data){
            return data.map((item) => {
                return(
                    <option value={item.city}>{item.name} | {item.city_name}</option>
                )
            })
        }
    }
    renderRest = (data)=>{
        if(data){
            return data.map((item) => {
                return(
                    <option value={item._id}>{item.name} | {item.locality}</option>
                )
            })
        }
    }
    handleCity=(event)=>{
        console.log(event.target.value)
        const cityId= event.target.value;
        fetch(`${rurl}${cityId}`,{method:'GET'})
        .then((res)=> res.json())
        .then((data)=>{
            this.setState({rest:data})
        })
    }
    handleRest=(event)=>{
        this.props.history.push(`/details/${event.target.value}`)
    }
    render(){
        return(
            <React.Fragment>
                <div className="food_image">
                    <div className="social_media">
                        
                        <a  href="www.facebook.com" ><img id="fy_font" src="images\facebook.png" /></a>
                        <a   href="www.youtube.com" ><img id="fy_font" src="images\youtube.png" /></a>
                    </div>
                    <div className="logo">
                            <img src="images/foodfly.png" style={{height : 75}} />
                    </div>
                    <div id="title_heading">
                        <b>Find Best Restaurants,Cafes,Bars</b>
                    </div>
                    <div className="location_dropdown">
                        <select  className="dropdown" onChange={this.handleCity}>
                            <option >===Select City====</option>
                            {this.renderCity(this.state.city)}
                            
                        </select>
                        <select className="dropdown" id="" name="restaurants" onChange={this.handleRest}>
                            <option>====Select Restaurant===</option>
                            {this.renderRest(this.state.rest)}

                        </select>
                    </div>
                </div>
                
            </React.Fragment>
        )
    }
    //api to get data
    componentDidMount(){
        
        fetch(url,{method:'GET'})
        .then((res) => res.json())
        .then((data)=> this.setState({city:data}))
        console.log(this.state.city)

    }
}

export default withRouter(Search);
