
import React from 'react';
import {Link} from 'react-router-dom';
import './Header.css';

const Header =(props) => {
    return(
        <>
            <div className="navbar">
                <nav class="navbar navbar-inverse">
                    <ul className="nav navbar-nav" className="imglogo">
                        <Link to="/">
                            <p className="glyphicon glyphicon-home" id="home"></p>   
                        </Link>
                    </ul>
                    <ul className="nav navbar-nav navbar-right">
                        <p className="navbarright">FoodFly</p>
                    </ul>
                </nav>
            </div>
        </>
    )
}

export default Header;