import React from 'react';
import {Link} from 'react-router-dom';
import './listing.css';

const listingDisplay = (props) =>{
    const renderList = ({restaurantList})=>{
        if(restaurantList){
            if(restaurantList.length>0){
            return restaurantList.map((item)=>{
                return(
                <>
                    <div className="restaurants">
                        <div className="rest_img">
                            <img  src={item.thumb}  alt="BreakFast" />
                        </div>
                        <div  className="rest_heading">
                            <Link to={`/details/${item._id}`}>
                                <div className="heading"> 
                                {item.name}
                                </div>
                            </Link>
                            <div className="subheading"> 
                                Fort
                            </div>
                            <div  className="address"> 
                                {item.address}
                            </div>
                        </div>
                        
                        <div className="cost">
                            <hr className="hrline"/>
                            <p>Cuisine : Bakery</p>
                            <p>Cost  : {item.cost}/-</p>

                        </div>
                    </div>
                </>
            )
            })
        }
        else{
            return(
                <div>
                    <center>
                        <h2>No Restaurants On This Filter</h2>
                    </center>
                </div>
            )
        }
        }
        else{
            return(
                <div>
                 <img src = "/images/loader_gif.gif"/>
                </div>
            )
            
        }
    }
    return(
        <>
        {renderList(props)}
        </>
    )
}
export default listingDisplay;
