import React from 'react';
import "./QuickSearch.css";
import {Link} from 'react-router-dom';
import Footer from './../../Footer';

const QuickSearch =(props) => {
        const renderList = ({quickData}) =>{
            if(quickData){
                return quickData.map((item)=>{
                    return(
                        <>
                            <Link to ={`/listing/${item._id}`} >
                                <div className="col-md-3 tilecontainer">
                                    <div className="tilecomponent1">
                                        <img src={`/images/${item.name}.png`} className=" img-responsive imgstyle"  />
                                    </div>
                                    <div className="tilecomponent2">
                                        <div className="heading">
                                            {item.name}
                                        </div>
                                        <div className="subheading">
                                            Start Your Day with Exclusive {item.name} Options
                                        </div>  
                                    </div>
                                </div>
                            </Link>
                            
                        </>
                    )
                   
                })
            }
            
        }
        return(
            <React.Fragment>
                <div className="quickSearchContainer"> 
                    <p className="quickSearchHeading">
                        Quick Searches
                    </p>
                    <p className="quickSearchSubHeading">
                        Discover RÃ©staurants By Meal Type
                    </p>
                    {renderList(props)}
                </div>
            </React.Fragment>
        )
    
}

export default QuickSearch;